@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

@mixin mobile{
    @media screen and (max-width: 480px) {
        @content
    }
}
@mixin tablet{
    @media screen and (max-width: 768px) {
        @content
    }
}
@mixin laptop{
    @media screen and (max-width: 1200px) {
        @content
    }
}
*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins' , sans-serif;
}
.formContainer{
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper{
        background-color: white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        .logo{
            color: #5d5b8d;
            font-weight: bold;
            font-size: 24px;
        }
        .title{
            color: #5d5b8d;
            font-size: 12px;
        }
        .form{
            display: flex;
            flex-direction: column;
            gap: 15px;

            input{
                padding: 15px;
                border: none;
                border-bottom: 1px solid  #a7bcff;
                width: 250px;
                &::placeholder{
                    color: rgb(175,175,175);
                }
                outline: none;
            }

            label{

                display: flex;
                gap: 5px;
                justify-content: start;
                align-items: center;
                cursor: pointer;
                font-size: 12px;
                color: #8da4f1;

                .img-icon{
                    font-size: 1.5rem;
                }
            }

            button{
                background-color: #7b96ec;
                color: white;
                padding: 10px;
                border: none;
                cursor: pointer;
            }
        }
        p{
            color: #5d5b8d;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

.home{
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    .container{
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90%;
        }

        .sideBar{
            flex: 1;
            border-right: 1px solid #3e3c61;
            background-color: #3e3c61;
            position: relative;

            .navbar{
                display: flex;
                align-items: center;
                background-color: #2f2d52;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: #ddddf7;

                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }
                }

                .user{
                    display: flex;
                    gap: 10px;
                    img{
                        background-color: #ddddf7;
                        height: 24px;
                        width: 24px;
                        border-radius: 50%;
                        object-fit: cover;
                    }
                    button{
                        background-color: #5d5b8d;
                        color: #ddddf7;
                        font-size: 10px;
                        border: none;
                        padding: 5px;
                        border-radius: 5px;
                        cursor: pointer;
                        @include tablet{
                            position: absolute;
                            bottom: 10px;
                            right: 10px;
                        }
                    }
                }
            }
            .search{
                .searchForm{
                    padding: 8px;
                    font-size: 1rem;
                    border-bottom: 1px solid gray;
                    input{
                        background-color: transparent;
                        border: none;
                        outline: none;
                        color: white;
                        &::placeholder{
                            color: lightgray;
                        }
                    }
                }
            }
            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;
                .userChatInfo{
                    span{
                        font-weight: 500;
                        font-size: 1.1rem;
                    }
                    p{
                        font-weight: 300;
                        font-size: 0.9rem;
                        color: lightgray;
                    }
                }
                &:hover{
                    background-color: #2f2d52;
                }
                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }
            }
        }

        .chat{
            flex: 2;
            .chatInfo{
                height: 50px;
                padding: 10px;
                background-color: #5d5b8d;
                color: lightgray;
                display: flex;
                justify-content: space-between;
                align-items: center;

                .chatIcons{
                    color: white;
                    display: flex;
                    gap: 15px;
                    font-size: 1.3rem;
                }
            }

            .messages{
                background-color: #ddddf7;
                padding: 10px;
                height: calc(100% - 160px); // important
                overflow: scroll;
                .message{
                    display: flex;
                    gap: 10px;
                    margin-bottom: 20px;
                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                        font-size: 0.9rem;
                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0 10px 10px 10px;
                            max-width: max-content; // important
                        }

                    }

                    &.owner{
                        flex-direction: row-reverse;
                        .messageContent{
                            align-items: flex-end;
                            p{
                                background-color: #8da4f1;
                                color: white;
                                border-radius: 10px 0 10px 10px;
                            }
                        }
                    }
                }
            }

            .input{
                background-color: white;
                height: 50px;
                padding: 10px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                input{
                    width: 100%;
                    outline: none;
                    border: none;
                    font-size: 1.1rem;
                    color: #2f2d52;
                    &::placeholder{
                        color: darkgray;
                    }
                }

                .send{
                    cursor: pointer;
                    display: flex;
                    gap: 10px;
                    font-size: 1.5rem;
                    align-items: center;
                    button{
                        outline: none;
                        border: none;
                        background-color: #8da4f1;
                        color: white;
                        padding: 10px 20px;
                        border-radius: 10px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}